import { composeMongoose } from 'graphql-compose-mongoose';
import { Schema, Model, Types, Document, model } from 'mongoose';
import mongooseAlgolia from 'mongoose-algolia';

export type ProviderEnum = 'credolab' | 'suni';

export interface ICreditScore {
  _id?: any;
  user: string;
  referenceNumber: string;
  value: number;
  provider: ProviderEnum;
  active?: boolean;
}

export type CreditScoreDocument = Document<Types.ObjectId, any, ICreditScore> &
  ICreditScore;

export const creditScoreSchema = new Schema<ICreditScore>(
  {
    user: {
      type: String,
      required: [true, 'Please insert a user'],
    },
    referenceNumber: {
      type: String,
    },
    value: {
      type: Number,
    },
    provider: {
      type: String,
      enum: ['credolab', 'suni'],
      required: [true, 'Please insert the provider'],
    },
    active: {
      type: Boolean,
      default: true,
    },
  },
  {
    timestamps: true,
  }
);

creditScoreSchema.plugin(mongooseAlgolia, {
  appId: process.env.ALGOLIA_APP_ID,
  apiKey: process.env.ALGOLIA_PRIVATE_KEY,
  indexName: 'creditScore',
  selector: '-createdAt -updatedAt',
  debug: true,
});

export const CreditScore = model<
  ICreditScore,
  Model<ICreditScore> & {
    SyncToAlgolia?: any;
    SetAlgoliaSettings?: any;
  }
>('CreditScore', creditScoreSchema);

export const CreditScoreTC = composeMongoose<CreditScoreDocument>(
  CreditScore as any
);
